
  var scan = null
  import { mapGetters, mapActions } from 'vuex'
  import XHeader from 'vux/src/components/x-header/index.vue'

  export default {
  	components: {
      XHeader
    },
    data () {
      return {
      }
    },
    computed: {
      ...mapGetters({
        StationChk: 'QueryStationChk'
      })
    },
    methods: {
      ...mapActions([
        'GetQrCodeChk',
        'GetTicketChk',
        'QueryStationChk'
      ]),
    },
    mounted () {
      const $this = this
	  const onmarked = function(type, result) {
	    var text = '未知: ';
	    switch (type) {
	      case plus.barcode.QR:
	        text = 'QR: ';
	        break;
	      case plus.barcode.EAN13:
	        text = 'EAN13: ';
	        break;
	      case plus.barcode.EAN8:
	        text = 'EAN8: ';
	        break;
	    }
	    if (result == "") {
	      alert('验票码无效')
	    } else {
	        try{
//	        	result = result.split('#')
		        let shiftId = $this.StationChk.shiftId
		        let stationId = $this.StationChk.stationId
		        let params = {
		           shiftId: shiftId,
		           stationId: stationId,
		           ticketInfos: [
		             {'qrCode': result}
		           ]
		        }
		        $this.GetTicketChk(params).then((res) => {
		            alert(res.status.desc)
		            scan.start()
		        })
		    }catch(e){
		    	alert(result)
		        scan.start()
	        }
	    }
	  }
	  const startRecognize = function() {
	    scan = new plus.barcode.Barcode('bcid');
	    scan.onmarked = onmarked;
	    scan.start()
	  }
	  /*
	      创建二维码扫描器
	   * */
	    var e = document.getElementById("scan");
	    e.removeAttribute("disabled")
	    setTimeout(() => {
	      this.$store.commit('isLoading', {isLoading: false})
	      startRecognize()
	    },500)
   },
   created () {
      this.$store.commit('isLoading', {isLoading: true})
   },
   destroyed () {
   	 // 取消扫描
     scan.close()
   }
  }



